"use client";
import { useEffect } from "react";
import { useRouter } from "next/navigation";
import useAuthStore from "@/store/authStore";
import AdminLayout from "@/components/admin/AdminLayout";
import UsersListTable from "@/components/admin/UsersListTable";

export default function UsersListPage() {
  const router = useRouter();
  const { user, isAuthenticated, isLoading } = useAuthStore();

  useEffect(() => {
    if (!isLoading && !isAuthenticated) router.push("/login");
  }, [isAuthenticated, isLoading, router]);

  useEffect(() => {
    if (isAuthenticated && user && user.role !== 'admin') router.push("/dashboard");
  }, [isAuthenticated, user, router]);

  if (isLoading) return <div className="min-h-screen flex items-center justify-center"><div className="animate-spin rounded-full h-32 w-32 border-b-2 border-green-500"></div></div>;
  if (!isAuthenticated || user?.role !== 'admin') return null;

  return <AdminLayout><UsersListTable /></AdminLayout>;
}




